    import {uniCalendar} from "@/components/uni-calendar/uni-calendar"
	export default {
        components: {
            uniCalendar
        },
		data() {
			return {
				nowtime: ''
			}
		},
		onLoad() {
            let date = new Date
            let Y = date.getFullYear() + '-'
            let M = date.getMonth() + 1 + '-'
            let D = date.getDate();
            let nowtime = Y + M + D
            this.nowtime = String(nowtime)
            console.log(String(nowtime), 'nowtime')
		},
		methods: {
			change(e) {
                console.log(e);
            },
            monthSwitch(e) {
                console.log(e)
            }
		}
	}
